@use "../variables" as var;

.about-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: white;
}

.about-section {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 100%;
  padding: 5vh 10vw;

  font-family: "Fira Mono", monospace;
  text-align: center;

  border-bottom: 2px solid var.$blue;

  background: linear-gradient(var.$black-rgba, var.$black-rgba),
    url(var.$geo-bkg);
  background-repeat: no-repeat;
  background-size: cover;
}

.about-title {
  margin-bottom: 5vh;
  font-size: 3rem;
  font-family: var.$cuprum;
  text-transform: uppercase;
  letter-spacing: 4px;

  color: white;
}

.about-content-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-image-container {
  height: auto;
  margin-bottom: 5vh;
  max-width: 250px;
}

.about-img {
  width: 100%;

  border-radius: 20px;
}

.about-info-text {
  text-align: justify;
}

.about-info-text {
  color: whitesmoke;
  opacity: 0.85;
  font-size: 0.9rem;
  font-family: var.$viga;
}

.info-list-item {
  text-transform: capitalize;
  // text-align: center;
  padding-left: 12vw;
}

.info-list-item:nth-child(1) {
  color: rgb(36, 165, 220);
}
.info-list-item:nth-child(2) {
  color: rgb(223, 128, 46);
}
.info-list-item:nth-child(3) {
  color: rgb(37, 209, 60);
}
.info-list-item:nth-child(4) {
  color: rgb(240, 68, 25);
}
.info-list-item:nth-child(6) {
  color: rgb(166, 36, 231);
}
.info-list-item:nth-child(5) {
  color: rgb(214, 208, 25);
}

.info-list-item:nth-child(7) {
  color: var.$blue;
}

// BREAKPOINT 700px

@media screen and (min-width: 700px) {
  .about-content-wrapper {
    display: block;
    width: 100%;
  }

  .about-image-container {
    width: 30vw;
    float: left;
    margin: 5vh 5vw;
  }

  .about-info-text {
    font-size: 1.25rem;
    // line-height: 1.5em;
  }
}

// BREAKPOINT 1000px
@media screen and (min-width: 1000px) {
  .about-title {
    margin: 5vh;
    font-size: 5rem;
    letter-spacing: 6px;
  }

  .about-content-wrapper {
    max-width: 900px;
    letter-spacing: 2px;
  }

  .about-image-container {
    max-width: 350px;
    margin-left: -5%;
  }

  .about-info-text {
    // font-size: 1.15rem;
    line-height: 1.8em;
    margin-bottom: 40px;
  }

  .info-list-item {
    font-size: 1.5rem;
  }
}
